<template>
  <div>
    <div class="fixed-bar">
      <mu-appbar title="">
        <mu-icon-button icon='arrow_back' @click='back'  slot='left'/>
      </mu-appbar>
    </div>
    <div class="order_sending">
			<h3 class="order_sending_h3"></h3>
			<div class="order_sending_address" style="font-size:12px">
				<p>
					<span class="sending_fl"></span><span class="sending_fr">{{data.shipName}}
					</span>
				</p>
				<p v-if="data.shipMobile !== null">
					<span class="sending_fl"></span><span class="receiving_fr">{{data.shipMobile}}</span>
				</p>
				<p>
					<span class="sending_fl"></span><span class="sending_fr">{{data.shipArea}}{{data.shipAddress}}</span>
				</p>
			</div>

      <mu-select-field v-model="order.exp" :labelFocusClass="['label-foucs']" label="">
        <mu-menu-item v-for="v,index in middle" :value="v.com" :title="v.name" />
      </mu-select-field>
      <mu-text-field label="" hintText="" v-model="order.cost" fullWidth/>
      <mu-text-field label="" hintText="" v-model="order.deliverySn" fullWidth/>
      <mu-raised-button label="" @click="submit" fullWidth primary/>
		</div>
  </div>
</template>
<script>
import qs from 'qs'
export default {
  data () {
    return {
      data: {},
      middle: [],
      order: {}
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.get()
    })
  },
  methods: {
    back () {
      this.$router.back()
    },
    get () {
      this.$http.get('/rest/api/order/detail/' + this.$route.params.id).then((res) => {
        this.data = res.data.attributes.data
        this.middle = res.data.attributes.middle
        this.order.orderId = this.data.orderId
      })
    },
    submit () {
      if (!this.order.exp) {
        window.alert('')
        return
      }
      if (!this.order.cost) {
        window.alert('')
        return
      }
      if (!this.order.deliverySn) {
        window.alert('')
        return
      }
      this.$http.post('/rest/api/order/express?' + qs.stringify(this.order)).then((res) => {
        this.$router.push({
          path: '/bind',
          query: this.bind
        })
      })
    }
  }
}
</script>



