<template>
  <div>
    <div class="fixed-bar">
      <mu-appbar title="">
        <mu-icon-button icon='arrow_back' @click='back' slot='left'/>
      </mu-appbar>
    </div>
    <div class="container p10">
      <mu-text-field label="" hintText="" v-model="sf.fdbk_subject64" fullWidth/>
      <mu-select-field v-model="sf.fdbk_type" :labelFocusClass="['label-foucs']" label="">
        <mu-menu-item v-for="v,index in list" :value="v.value" :title="v.text" />
      </mu-select-field>
      <mu-text-field hintText="" v-model="sf.fdbk_intro1k" fullWidth multiLine :rows="8" :rowsMax="10" fullWidth/>
      <mu-raised-button label="" @click="submit" secondary fullWidth/>
    </div>
  </div>
</template>
<script>
import qs from 'qs'
export default {
  data () {
    return {
      sf: {
        card_no: this.$store.state.user.username,
        fdbk_subject64: '',
        fdbk_intro1k: '',
        fdbk_type: '2'
      },
      list: [
        { text: '', value: '1' },
        { text: '', value: '2' },
        { text: '', value: '3' },
        { text: '', value: '4' },
        { text: '', value: '5' },
        { text: '', value: '6' }
      ]
    }
  },
  methods: {
    back () {
      this.$router.back()
    },
    submit () {
      if (this.sf.fdbk_subject64 === '') {
        window.alert('')
        return false
      }
      if (this.sf.fdbk_intro1k === '') {
        window.alert('')
        return false
      }
      this.sf.fdbk_subject64 = this.sf.fdbk_subject64 + '[APP]'
      this.$http.post('http://crmyun.jihui88.com:9500/api/jihuifeedback.php', qs.stringify(this.sf)).then((res) => {
        window.alert('')
      })
      this.sf.fdbk_subject64 = ''
    }

  }
}
</script>



