<template>
<div>
  <div class='fixed-bar'>
    <mu-appbar title=''>
      <mu-icon-button icon='arrow_back' @click='back' slot='left'/>
    </mu-appbar>
  </div>
  <div class='extend_tip'>
    <span class='tip'></span><br>
    <a href='#/spreadRank' class='extend_tip_a'></a>
  </div>
  <mu-tabs :value='activeTab' @change='handleTabChange' class='view-tabs'>
    <mu-tab v-for="item in tabs" :value="item.value" :title="item.title"/>
  </mu-tabs>
  <template v-for='item in list'>
    <div class='extend' v-if='item.posterCate === activeTab'>
      <img class='extend_img' :src="item.pic">
      <p class='extend_desc'>{{item.posterDesc}}</p>
      <mu-raised-button label=',' @click='saveImg(item)' secondary fullWidth/>
    </div>
  </template>
  <div class="p10">
    <div style="text-align: center;font-size: 0.7rem;padding: 0.5rem 0;">
      
    </div>
    <mu-text-field label="" hintText="" v-model="m.name" fullWidth/>
    <mu-text-field label="" hintText="" v-model="m.company" fullWidth/>
    <mu-text-field label="" hintText="" v-model="m.phone" fullWidth/>
    <mu-text-field label="" hintText="" v-model="m.referee" fullWidth/>
    <mu-text-field hintText="..." v-model="m.content" fullWidth multiLine :rows="8" :rowsMax="10" fullWidth/>
    <mu-raised-button label="" @click="submit" secondary fullWidth/>
  </div>
  <img v-if="pic !== ''" :src="pic" class="downPic" @click="downPic" alt="">
</div>
</template>
<script>
import qs from 'qs'
export default {
  data () {
    return {
      list: [],
      activeTab: '0',
      tabs: [
        {value: '0', title: ''},
        {value: '1', title: ''},
        {value: '2', title: ''},
        {value: '3', title: ''}
      ],
      pic: '',
      m: {},
      sf: {
        card_no: this.$store.state.user.username,
        fdbk_subject64: '[APP]',
        fdbk_type: '5'
      }
    }
  },
  created () {
    this.get()
  },
  methods: {
    back () {
      this.$router.back()
    },
    get () {
      this.$http.get('/rest/api/poster/list?pageSize=72').then((res) => {
        for (var item of res.data.attributes.data) {
          item.pic = 'http://img.jihui88.com/' + item.pic
          this.list.push(item)
        }
      })
    },
    handleTabChange (val) {
      this.activeTab = val
    },
    downPic () {
      this.pic = ''
    },
    saveImg (p) {
      var ctx = this
      var data = {
        pic: p.pic,
        qrcodeWidth: p.qrcodeWidth,
        qrcodeHeight: p.qrcodeHeight,
        username: 'ggggfj',
        qrcodeLeft: p.qrcodeLeft,
        qrcodeTop: p.qrcodeTop,
        posterId: p.posterId,
        qrcode: 'http://wcd.jihui88.com/rest/comm/qrbar/create?w=' + p.qrcodeWidth + '&text=http://www.jihui88.com/member/reg_m.html?d=' + this.sf.card_no
      }
      setTimeout(function () {
        ctx.pic = 'https://api.jihui88.net/qrcode_poster/posters/' + p.posterId + '.jpg'
      }, 3000)
      this.$http.post('http://api.jihui88.net/qrcode_poster/api/poster', qs.stringify(data)).then((res) => {
        ctx.pic = 'https://api.jihui88.net/qrcode_poster/posters/' + p.posterId + '.jpg'
      })
    },
    submit () {
      if (!this.m.name) { return window.alert('') }
      if (!this.m.company) { return window.alert('') }
      if (!this.m.phone) { return window.alert('') }
      if (!this.m.referee) { return window.alert('') }
      this.sf.fdbk_intro1k = ':' + this.m.name + '\n:' + this.m.company + '\n:' + this.m.phone +
      '\n:' + this.m.referee + '\n:' + this.m.content
      this.$http.post('http://crmyun.jihui88.com:9500/api/jihuifeedback.php', qs.stringify(this.sf)).then((res) => {
        window.alert('')
        this.m = {}
      })
    }
  }
}
</script>
<style scoped>
.extend_tip {
    background: #f5f5f5;
    padding: 10px;
    text-indent: 25px;
    color: #666;
    line-height: 1.5;
    font-size: 0.5rem;
    font-family: 'Microsoft YaHei',Helvetica,Arial,'WenQuanYi Micro Hei';
}
.extend_tip_a {
    color: #ff7300;
    padding: 10px 0 5px 0;
    display: block;
    font-family: 'Microsoft YaHei',Helvetica,Arial,'WenQuanYi Micro Hei';
    font-size: 0.6rem;
}
.extend{
    padding: 15px;
}
.extend_img {
    width: 100%;
}
.extend_desc {
    color: #666;
    padding: 15px 5px;
}
.downPic{
  width: 100vw;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 9;
}
</style>



