<template>
  <div>
    <mu-appbar title="">
      <mu-icon-button icon='arrow_back' @click="back"  slot="left"/>
    </mu-appbar>
    <div class="container p10">
      <mu-select-field v-model="category.belongId" :labelFocusClass="['label-foucs']" label="">
        <mu-menu-item v-for="v,index in categorySelect" :value="v.categoryId" :title="v.name" />
      </mu-select-field>
      <p style="color: #ff6000;"></p>
      <mu-text-field label="" hintText="" v-model="category.name" fullWidth/>

      <mu-raised-button label="" @click="submit" class="demo-raised-button" secondary fullWidth/>
    </div>

  </div>
</template>
<script>
import qs from 'qs'
import { mapMutations } from 'vuex'
export default {
  data () {
    return {
      category: {},
      categorySelect: []
    }
  },
  created () {
    this.get()
  },
  methods: {
    ...mapMutations(['showLoading', 'hideLoading']),
    back () {
      this.$router.back()
    },
    get () {
      this.category.type = this.$route.params.id
      this.$http.get('/rest/api/category/rank/?type=' + this.$route.params.id).then((res) => {
        this.categorySelect = res.data.attributes.data
      })
    },
    submit () {
      this.showLoading()
      this.$http.put('/rest/api/category/detail?' + qs.stringify(this.category)).then((res) => {
        this.hideLoading()
        window.alert('')
        this.$router.back()
      })
    }
  }
}
</script>
<style lang="less" scoped>

</style>



