<template>
  <div>
    <div class="fixed-bar">
      <mu-appbar title="">
        <mu-icon-button icon='arrow_back' @click='back' slot='left'/>
      </mu-appbar>
    </div>
    <mu-list>
      <mu-list-item title="" href="#/account">
        <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
      </mu-list-item>
      <mu-divider />
      <mu-list-item title="" href="#/service_feedback">
        <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
      </mu-list-item>
      <mu-divider />
      <mu-list-item title="" href="#/log">
        <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
      </mu-list-item>
      <mu-divider />
    </mu-list>
    <mu-list>
      <mu-list-item title="" href="http://m.jihui88.com/">
        <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
      </mu-list-item>
      <mu-divider />
      <mu-list-item title="" href="#/sale">
        <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
      </mu-list-item>
      <mu-divider />
      <mu-list-item title="" href="#/service">
        <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
      </mu-list-item>
      <mu-divider />
      <mu-list-item title="" href="#/about">
        <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
      </mu-list-item>
      <mu-divider />
    </mu-list>
    <mu-raised-button label="" @click="signout" secondary fullWidth/>

  </div>
</template>
<script>
import { mapMutations } from 'vuex'
export default {
  methods: {
    ...mapMutations(['showLoading', 'hideLoading']),
    back () {
      this.$router.back()
    },
    signout () {
      var ctx = this
      this.showLoading()
      this.$http.get('/rest/api/user/logout').then((res) => {
        ctx.hideLoading()
        ctx.$store.state.user = null
        ctx.$router.push({path: '/login'})
      })
    }

  }
}
</script>



